import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { YoutubeService } from './youtube.service';

@Component({
    templateUrl: 'youtube.component.html',
    providers: [YoutubeService]
})

export class YoutubeComponent implements OnInit {
    list: any[];
    mode: string = 'trending';
    query: string = '';
    url: any;
    message: string = '';

    constructor(
        private router: Router,
        private rt: ActivatedRoute,
        private service: YoutubeService) {}

    public ngOnInit(): void {
        this.getMode();
	}
	
	getMode() {
        const m = localStorage.getItem('youtube-mode');
        if (m) {
            this.mode = m;
        }

        switch (this.mode) {
            case 'trending':
                this.getTrending();
                break;
            case 'search':
                this.getQuery();
                this.getSearch();
                break;
            case 'channel':
                const c = localStorage.getItem('youtube-channel');
                this.getChannel(c);
                break;
        }
    }

    getTrending() {
		this.message = null;
        this.list = [];
        this.setMode('trending');
        let e: any;
        this.service.getTrending()
            .subscribe(
            data => this.list = data.items,
            error => e = error
            );
    }

    getSearch() {
		this.message = null;
        this.list = [];
        this.setMode('search');
        this.setQuery();
        let e: any;
        this.service.getSearch(this.query)
            .subscribe(
            data => this.list = data.items,
            error => e = error
            );
    }

    getChannel(channelId) {
		this.message = null;
        this.list = [];
        this.setMode('channel');
        let e: any;
        this.service.getChannel(channelId)
            .subscribe(
				data => this.list = data.items,
				error => e = error
            );
    }

    goSearch = function ($event) {
        const keypressed = $event.keyCode || $event.which;
        if (keypressed === 13) {
            this.getSearch();
        }
	}
	
	togglePlayer(id, index) {
		for (let i = 0; i < this.list.length; i++) { 
			if (i != index) {
				this.list[i].mode = "closed";
			}
		}

		if (this.list[index].mode != "player") {
			this.list[index].mode = "player";
		} else {
			this.list[index].mode = "closed";
		}
	}

    channel(id) {
        localStorage.setItem('youtube-channel', id);
        this.getChannel(id);
    }

    setMode(m) {
        this.mode = m;
        localStorage.setItem('youtube-mode', m);
    }

    setQuery() {
        localStorage.setItem('youtube-query', this.query);
    }

    getQuery() {
        const q = localStorage.getItem('youtube-query');
        if (q) {
            this.query = q;
        }
    }
}