import { Injectable } from '@angular/core';
import { Http, Jsonp } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable()
export class PhotoListService {
    constructor(
	  private http: Http, 
      private jsonp: Jsonp) { }

	getAlbums(accessToken, startIndex) {
		var url = 'https://photoslibrary.googleapis.com/v1/albums/list?access_token=' + accessToken;
		return this.jsonp.get(url)
		.pipe(map(res => res.json()));
	}

	getPhotos(accessToken, startIndex, albumId) {
		var url = 'https://picasaweb.google.com/' +
			'data/feed/api/user/default/albumid/' + albumId + '?' +
			'kind=photo&' +
			'access=all&' +
			'start-index=' + startIndex + '&' +
			'max-results=1000&' +
			'alt=json&' +
			'callback=JSONP_CALLBACK&' +
			'access_token=' + accessToken + '&' +
			'fields=openSearch:totalResults,entry(title,published,updated,gphoto:id,gphoto:timestamp,' +
			'gphoto:access,gphoto:location,gphoto:originalvideo,media:group(media:keywords,media:description,media:thumbnail,media:content))';
		return this.jsonp.get(url)
		.pipe(map(res => res.json()));
	}

    getUser(accessToken) {
        return this.http.get('https://people.googleapis.com/v1/people/me?personFields=names&' +
        'access_token=' + accessToken)
				.pipe(map(res => res.json()));
	}
}