import { Component } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent {
  mode: string = 'home';

  constructor(private router: Router) {

    router.events.subscribe(e => {
      if (e instanceof NavigationEnd) {
        if (e.url == '/') {
          this.mode = 'home';
        } else if (e.url.indexOf('privacy') > -1) {
          this.mode = 'privacy';
        } else if (e.url.indexOf('terms') > -1) {
          this.mode = 'terms';
        } else if (e.url.indexOf('contact') > -1) {
          this.mode = 'contact';
        } else if (e.url.indexOf('youtube') > -1) {
          this.mode = 'youtube';
        } else if (e.url.indexOf('video-list') > -1) {
          this.mode = 'video-list';
        }
      }
    });
  }
}