import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { PhotoListService } from './photo-list.service';

@Component({
    templateUrl: 'photo-list.component.html',
    providers: [PhotoListService]
})

export class PhotoListComponent implements OnInit {	
	user: string = '';	
	accessToken: string = '';
	url: any;
    message: string = '';
	albums: any[] = [];
	photos: any[] = [];
	albumNum: number = 0;
	albumCount: number = 0;

    constructor(
        private router: Router,
        private rt: ActivatedRoute,
        private sanitizer: DomSanitizer,
        private service: PhotoListService) {}

    public ngOnInit(): void {
        this.setUrl();
        this.getToken();
    }

    setUrl() {
        let site = '';
        if (location.host === 'sharefeed.org') {
            site = 'sharefeed';
        } else {
            site = 'local';
		}

        this.url = this.sanitizer.bypassSecurityTrustResourceUrl(
            'https://accounts.google.com/o/oauth2/v2/auth?' +
            'client_id=830231751631-nev29dca0niv3419sq6c88hqlsttci70.apps.googleusercontent.com&' +
            'redirect_uri=http://localhost:89/picasa/' + site + '/&' +
            'response_type=token&' +
            'scope=profile https://picasaweb.google.com/data&' +
            'include_granted_scopes=true');
    }

    getToken() {
        let a = this.rt.snapshot.params['access_token'];
        if (!a) {
            a = localStorage.getItem('picasa_access_token');
        } else {
            a = a.replace('&token_type', '');
            localStorage.setItem('picasa_access_token', a);
        }

		this.accessToken = a;
        this.getUser();
    }
    
    getUser() {
        let d: any;
        let e: any;
        this.service.getUser(this.accessToken)
            .subscribe(
            data => d = data,
            error => e = error,
            () => this.loadUser(d)
            );
	}
	
	loadUser(d) {
		if (d.meta && d.meta.error_message) {
            this.message = d.meta.error_message;
        } else {
            this.user = d.names[0].displayName;
		}
        this.getAlbums(0);
    }

    logout() {
		localStorage.removeItem('youtube_access_token');
        this.user = '';
	}

	import() {
		this.albums = [];
		this.getAlbums(1);
	}

	getAlbums(startIndex) {
        let d: any;
        let e: any;
        this.service.getAlbums(this.accessToken, startIndex)
            .subscribe(
				data => d = data,
				error => e = error,
				() => this.setAlbums(d, e, startIndex)
            );
	}

	setAlbums(data, error, startIndex) {
		let d = data.feed.entry;
		if (d) {
			if (this.albums.length === 0) {
				this.albums = d;
			} else {
				this.albums = this.albums.concat(d);
			}
			
			startIndex += 1000;
			this.getAlbums(startIndex);
		} else {
			this.exportAlbums();
			this.photos = [];
			this.albumCount = this.albums.length;
			this.getPhotosForAlbums(0);	
		}
	}

	getPhotosForAlbums(index) {
		this.albumNum = index + 1;
		if ((index + 1) >= this.albums.length) {
			this.exportPhotos();
		} else {
			let a = this.albums[index];
			let albumId = a.gphoto$id.$t.toString();
			this.getPhotos(index, 1, albumId);
		}
	}

	getPhotos(index, startIndex, albumId) {
        let d: any;
        let e: any;
        this.service.getPhotos(this.accessToken, startIndex, albumId)
            .subscribe(
				data => d = data,
				error => e = error,
				() => this.setPhotos(d, e, index, startIndex, albumId)
            );
	}

	setPhotos(data, error, index, startIndex, albumId) {
		let d = data.feed.entry;
		if (d) {
			if (this.photos.length === 0) {
				this.photos = d;
			} else {
				this.photos = this.photos.concat(d);
				if (this.photos.length > 10000) {
					this.exportPhotos();
				}
			}
			
			startIndex += 1000;
			if (startIndex > 10001) {
				this.getPhotosForAlbums(index + 1);		
			} else {
				this.getPhotos(index, startIndex, albumId);
			}			
		} else {
			this.getPhotosForAlbums(index + 1);	
		}
	}

	exportAlbums() {
		let csv = this.albumCSV();
		this.export(csv, 'albums.csv');
	}

	exportPhotos() {
		let csv = this.photoCSV();
		this.export(csv, 'photos.csv');
	}

    export(csv, filename) {
        if (!csv) return;

        if (!csv.match(/^data:text\/csv/i)) {
            csv = 'data:text/csv;charset=utf-8,' + csv;
        }
        let d = encodeURI(csv);

        let link = document.createElement('a');
        link.setAttribute('href', d);
        link.setAttribute('download', filename);
        link.click();
	}

	albumCSV() {
		let data = this.albums;
        if (!data || data.length ===0) {
            return null;
        }

		let r = 'Id,Title,Photos,Published,Updated,Access,Tags,Thumbnail\n';
		let ctr = 0;
        for (let i = 0; i < data.length; i++) {
			let x = data[i];
			r += '~' + x.gphoto$id.$t + ',';
			r += '~' + x.title.$t + ',';
			r += x.gphoto$numphotos.$t + ',';
			r += x.published.$t + ',';
			r += x.updated.$t + ',';
			r += x.gphoto$access.$t + ',';
			r += x.summary.$t + ',';
			r += '=HYPERLINK("' + x.media$group.media$thumbnail[0].url + '")\n';           
        }

        return r;
	}

	photoCSV() {
		let data = this.photos;
        if (!data || data.length ===0) {
            return null;
        }

		let r = 'PhotoId,AlbumId,Album,Title,Description,Timestamp,Published,Updated,Access,Tags,Type,Url,Thumbnail\n';
		let ctr = 0;
        for (let i = 0; i < data.length; i++) {
			let x = data[i];
			r += '~' + x.gphoto$id.$t + ',';
			r += '~' + '' + ',';
			r += '~' + '' + ',';
			r += '~' + x.title.$t + ',';
			r += ((x.media$group.media$description) ? x.media$group.media$description.$t : '') + ',';
			r += ((x.gphoto$timestamp) ? x.gphoto$timestamp.$t : '') + ',';
			r += x.published.$t + ',';
			r += x.updated.$t + ',';
			r += x.gphoto$access.$t + ',';
			r += ((x.media$group.media$keywords) ? x.media$group.media$keywords.$t : '') + ',';
			r += ((x.gphoto$originalvideo) ? 'video' : 'photo') + ',';
			r += '=HYPERLINK("' + ((x.media$group.media$content && x.media$group.media$content[1]) ? x.media$group.media$content[1].url : '') + '")';  
			r += '=HYPERLINK("' + x.media$group.media$thumbnail[0].url + '")\n';         
		}
		
		this.photos = [];

        return r;
	}
}        