import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable()
export class VideoListService {
    constructor(
      private http: Http) { }

    getMyVideos(accessToken, pageToken) {
		if (pageToken) {
			return this.http.get('https://www.googleapis.com/youtube/v3/search?' +
			'forMine=true&' +
			'type=video&' +
			'part=snippet&' +
			'fields=nextPageToken,items(id)&' +
			'maxResults=50&' +
			'pageToken=' + pageToken + '&' +
			'access_token=' + accessToken)
			.pipe(map(res => res.json()));
		} else {
			return this.http.get('https://www.googleapis.com/youtube/v3/search?' +
			'forMine=true&' +
			'type=video&' +
			'part=snippet&' +
			'fields=nextPageToken,items(id)&' +
			'maxResults=50&' +
			'access_token=' + accessToken)
			.pipe(map(res => res.json()));
		}
	}

	getVideos(accessToken, videoIdList) {
		return this.http.get('https://www.googleapis.com/youtube/v3/videos?id=' + videoIdList + '&' +
			'part=snippet,status&' +
			'fields=items(id,snippet(title,tags),status(privacyStatus,embeddable))&' +
			'access_token=' + accessToken)
			.pipe(map(res => res.json()));
	}

	getVideo(accessToken, videoId) {
		return this.http.get('https://www.googleapis.com/youtube/v3/videos?id=' + videoId + '&' +
			'part=snippet,status&' +
			'access_token=' + accessToken)
			.pipe(map(res => res.json()));
	}

    getUser(accessToken) {
        return this.http.get('https://people.googleapis.com/v1/people/me?personFields=names&' +
        'access_token=' + accessToken)
				.pipe(map(res => res.json()));
	}
	
	saveVideo(accessToken, body) {
		return this.http.put('https://www.googleapis.com/youtube/v3/videos?' +
			'part=snippet,status&' +
			'access_token=' + accessToken, body)
			.pipe(map(res => res.json()));
	}
}