import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { VideoListService } from './video-list.service';

@Component({
    templateUrl: 'video-list.component.html',
    providers: [VideoListService]
})

export class VideoListComponent implements OnInit {
	list: any[];
	matches: any[];
	page: any[];
    url: any;
    message: string = '';
	user: string = '';
	globalPrivacy: string = 'unlisted';
	title: string = '';
	tags: string = '';
	privacy: string = 'all';
	embeddable: string = 'all';
	accessToken: string = '';
	pageNum: number = 1;
	pageSize: number = 5;
	pageCount: number;

    constructor(
        private router: Router,
        private rt: ActivatedRoute,
        private sanitizer: DomSanitizer,
        private service: VideoListService) {}

    public ngOnInit(): void {
        this.setUrl();
        this.getToken();
    }

    setUrl() {
        let site = '';
        if (location.host === 'sharefeed.org') {
            site = 'sharefeed';
        } else {
            site = 'local';
        }

        this.url = this.sanitizer.bypassSecurityTrustResourceUrl(
            'https://accounts.google.com/o/oauth2/v2/auth?' +
            'client_id=830231751631-nev29dca0niv3419sq6c88hqlsttci70.apps.googleusercontent.com&' +
            'redirect_uri=http://localhost:89/youtube/' + site + '/&' +
            'response_type=token&' +
            'scope=profile https://www.googleapis.com/auth/youtube.force-ssl&' +
            'include_granted_scopes=true');
    }

    getToken() {
        let a = this.rt.snapshot.params['access_token'];
        if (!a) {
            a = localStorage.getItem('youtube_access_token');
        } else {
            a = a.replace('&token_type', '');
            localStorage.setItem('youtube_access_token', a);
        }

		this.accessToken = a;
        this.getUser();
    }
    
    getUser() {
        let d: any;
        let e: any;
        this.service.getUser(this.accessToken)
            .subscribe(
            data => d = data,
            error => e = error,
            () => this.loadUser(d)
            );
	}
	
	loadUser(d) {
        if (d.meta && d.meta.error_message) {
            this.message = d.meta.error_message;
        } else {
            this.user = d.names[0].displayName;
		}
        this.getMyVideos();
    }
	
	import() {
		this.message = null;
		this.list = [];
		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;
		this.page = [];
		this.title = null;
		this.tags = null;
		this.privacy = 'all';
		this.embeddable = 'all';
		this.getMyVideoData(null);
	}

    getMyVideos() {
		this.message = null;
		this.list = [];
		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;
		this.page = [];
		
		let d = localStorage.getItem("youtube-videos");
		if (d) {
			try {
				this.list = JSON.parse(d);
				this.matches = this.list;
				this.first();
			} catch (e) {
				console.error(e);
			}
		} else {
			this.getMyVideoData(null);
		}
	}

	getMyVideoData(pageToken) {
		let d: any;
		let e: any;
        this.service.getMyVideos(this.accessToken, pageToken)
            .subscribe(
				data => d = data,
				error => e = error,
				() => this.setMyVideoData(d)
            );
	}

	setMyVideoData(d) {
		let idList = [];
		for (let i = 0; i < d.items.length; i++) { 
			let vidId: string = '';
			if (d.items[i].id.videoId) {
				vidId = d.items[i].id.videoId;
			} else {
				vidId = d.items[i].id;
			}
			idList.push(vidId);
		}

		this.getVideoData(idList, d.nextPageToken);
	}

	getVideoData(idList, pageToken) {
		let d: any;
		let e: any;
        this.service.getVideos(this.accessToken, idList)
            .subscribe(
				data => d = data,
				error => e = error,
				() => this.setVideoData(d, pageToken)
            );
	}
	
	setVideoData(data, pageToken) {
		let d = data.items;
		for (let i = 0; i < d.length; i++) { 
			let item = {
				id: this.getVideoId(d[i].id),
				title: d[i].snippet.title,
				tags: d[i].snippet.tags,
				privacy: d[i].status.privacyStatus,
				embeddable: d[i].status.embeddable,
				mode: "closed",
				dirty: false
			}
			
			this.list.push(item);
		}
		
		if (pageToken) {
			this.getMyVideoData(pageToken);
		} else {
			this.sortData();
			this.matches = this.list;
			localStorage.setItem("youtube-videos", JSON.stringify(this.list));
			this.first();
		}
	}
	
	playVideo(index) {
		for (let i = 0; i < this.matches.length; i++) { 
			if (i != index) {
				this.matches[i].mode = "closed";
			}
		}

		this.page[index].mode = "player";
	}
	
	getVideoId(id) {
        let vidId: string = '';
        if (id.videoId) {
            vidId = id.videoId;
        } else {
            vidId = id;
        }

        return vidId;
    }

    logout() {
		localStorage.removeItem('youtube_access_token');
		localStorage.removeItem('youtube-videos');
		this.message = null;
        this.user = '';
		this.list = [];
		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;
		this.page = [];
	}
	
	sortData() {
		this.list.sort(function(a, b){
			var x = a.title.toLowerCase();
			var y = b.title.toLowerCase();
			if (x < y) {return -1;}
			if (x > y) {return 1;}
			return 0;
		});
	}

	paginate(page_size, page_number) {
		--page_number;
		this.page = this.matches.slice(page_number * page_size, (page_number + 1) * page_size);

		this.pageCount = Math.ceil(this.matches.length / this.pageSize)
	}

	searchTitle() {
		if (!this.title || this.title.length === 0) {
			this.matches = this.list;
			this.first();
			return;
		}

		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;

		for (let i = 0; i < this.list.length; i++) { 
			if (this.list[i].title.toLowerCase().indexOf(this.title.toLowerCase()) !== -1) {
				this.matches.push(this.list[i]);
			}
		}

		this.first();
	}

	searchTags() {
		if (!this.tags || this.tags.length === 0) {
			this.matches = this.list;
			this.first();
			return;
		}

		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;

		for (let i = 0; i < this.list.length; i++) { 
			if (this.list[i].tags && this.list[i].tags.toString().toLowerCase().indexOf(this.tags.toLowerCase()) !== -1) {
				this.matches.push(this.list[i]);
			}
		}

		this.first();
	}

	searchPrivacy() {	
		if (this.privacy === 'all') {
			this.matches = this.list;
			this.first();
			return;
		}
		
		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;

		for (let i = 0; i < this.list.length; i++) { 
			if (this.list[i].privacy === this.privacy) {
				this.matches.push(this.list[i]);
			}
		}

		this.first();
	}

	searchEmbeddable() {
		if (this.embeddable === 'all') {
			this.matches = this.list;
			this.first();
			return;
		}
		
		this.matches = [];
		this.pageNum = 1;
		this.pageCount = 0;

		let e: boolean = (this.embeddable === 'true')

		for (let i = 0; i < this.list.length; i++) { 
			if (this.list[i].embeddable === e) {
				this.matches.push(this.list[i]);
			}
		}

		this.first();
	}

	update(index) {
		this.page[index].dirty = true;
		let i = this.list.indexOf(o => o.id === this.page[index].id);
		this.list[i] = this.page[index];
	}

	save() {
		for (let i = 0; i < this.list.length; i++) { 
			if (this.list[i].dirty) {
				this.getVideo(this.list[i].id, i)
			}
		}
	}

	getVideo(id, index) {
		let d: any;
		let e: any;
        this.service.getVideo(this.accessToken, id)
            .subscribe(
            data => d = data.items[0],
			error => e = error,
			() => this.saveVideo(id, index, d)
        );
	}

	saveVideo(id, index, d) {
		let l = this.list[index];
		d.snippet.title = l.title;
		d.snippet.tags = l.tags;
		d.status.privacyStatus = l.privacy;
		d.status.embeddable = l.embeddable;

		let r: any;
		let e: any;
        this.service.saveVideo(this.accessToken, d)
            .subscribe(
            data => r = data,
			error => e = error,
			() => this.postSave(index)
        );	
	}

	postSave(index) {
		this.list[index].dirty = false;
		localStorage.setItem("youtube-videos", JSON.stringify(this.list));
	}

	globalPrivacySave() {
		for (let i = 0; i < this.matches.length; i++) { 
			this.getPrivacyVideo(this.matches[i].id, i)
		}
	}

	getPrivacyVideo(id, index) {
		let d: any;
		let e: any;
        this.service.getVideo(this.accessToken, id)
            .subscribe(
            data => d = data.items[0],
			error => e = error,
			() => this.savePrivacyVideo(id, index, d)
        );
	}

	savePrivacyVideo(id, index, d) {
		d.status.privacyStatus = this.globalPrivacy;

		let r: any;
		let e: any;
        this.service.saveVideo(this.accessToken, d)
            .subscribe(
            data => r = data,
			error => e = error
        );	
	}

	globalTitleSave() {
		for (let i = 0; i < this.matches.length; i++) {
			if (this.matches[i].tags && this.matches[i].tags.length === 2 && 
				this.matches[i].tags[1].indexOf('20') === 0 &&
				this.matches[i].title.indexOf('20') != 0
			) {
				this.getTitleVideo(this.matches[i].id, i)
			} 
		}
	}

	getTitleVideo(id, index) {
		let d: any;
		let e: any;
        this.service.getVideo(this.accessToken, id)
            .subscribe(
            data => d = data.items[0],
			error => e = error,
			() => this.saveTitleVideo(id, index, d)
        );
	}

	saveTitleVideo(id, index, d) {
		d.snippet.title = this.matches[index].tags[1] + ' - ' + this.matches[index].title;

		let r: any;
		let e: any;
        this.service.saveVideo(this.accessToken, d)
            .subscribe(
            data => r = data,
			error => e = error
        );	
	}

	jsonToCSV() {
        if (!this.matches || this.matches.length ===0) {
            return null;
        }

		let r = 'Title,Tags,Privacy,Embeddable,Url\n';
		let ctr = 0;
        for (let i = 0; i < this.matches.length; i++) {
			let x = this.matches[i];
			r += x.title + ',';
			r += this.getTags(x.tags) + ',';
			r += x.privacy + ',';
			r += x.embeddable + ',';
			r += '=HYPERLINK("https://www.youtube.com/watch?v=' + x.id + '")\n';           
        }

        return r;
	}
	
	getTags(tags) {
		try {
			if (tags) {
				return tags.join(' | ');
			} else {
				return '';
			}	
		} catch (e) {
			console.dir(tags);
		}		
	}

    export() {
        let csv = this.jsonToCSV();
        if (!csv) return;

        let filename = 'export.csv';

        if (!csv.match(/^data:text\/csv/i)) {
            csv = 'data:text/csv;charset=utf-8,' + csv;
        }
        let data = encodeURI(csv);

        let link = document.createElement('a');
        link.setAttribute('href', data);
        link.setAttribute('download', filename);
        link.click();
	}
	
	next() {
		if (this.pageNum === this.pageCount) return;
		this.pageNum++;
		this.paginate(this.pageSize, this.pageNum);
	}

	previous() {
		if (this.pageNum === 1) return;
		this.pageNum--;
		this.paginate(this.pageSize, this.pageNum);
	}

	first() {
		this.paginate(this.pageSize, this.pageNum);
	}
}